import { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import { useParams, useNavigate } from 'react-router-dom';
import {
    Alert,
    Box,
    Breadcrumbs,
    Button,
    Chip,
    FormControl,
    Grid,
    IconButton,
    InputLabel,
    MenuItem,
    Paper,
    Select,
    Snackbar,
    Tooltip,
    Typography,
    styled
} from '@mui/material';
import { useAuth } from 'src/contexts/AuthContext';
import {
    ArrowBack as BackIcon,
    Code as CodeIcon,
    Download as DownloadIcon,
    PlayArrow as RunIcon,
} from '@mui/icons-material';

import { projectService } from 'src/services/projectService';
import CodeEditor from 'src/components/CodeEditor';

const RootWrapper = styled(Box)(({ theme }) => ({
    display: "flex",
    width: '100%',
    flexDirection: 'column',
    height: `calc(100dvh - ${theme.header.mobileHeight})`,
    [theme.breakpoints.up("md")]: {
        height: `calc(100dvh - ${theme.header.height})`
    }
}));

const LANGUAGE_MAP = {
    javascript: {
        name: 'javascript',
        jdoodle: 'nodejs',
        versionIndex: '3',
        extension: 'js'
    },
    python: {
        name: 'python',
        jdoodle: 'python3',
        versionIndex: '3',
        extension: 'py'
    },
    java: {
        name: 'java',
        jdoodle: 'java',
        versionIndex: '3',
        extension: 'java'
    },
    php: {
        name: 'php',
        jdoodle: 'php',
        versionIndex: '3',
        extension: 'php'
    },
    go: {
        name: 'go',
        jdoodle: 'go',
        versionIndex: '3',
        extension: 'go'
    },
    rust: {
        name: 'rust',
        jdoodle: 'rust',
        versionIndex: '2',
        extension: 'rs'
    },
    sql: {
        name: 'sql',
        jdoodle: 'sql',
        versionIndex: '0',
        extension: 'sql'
    }
};

const ProjectRun = () => {
    const { projectId } = useParams();
    const navigate = useNavigate();
    const { user } = useAuth();
    
    const [project, setProject] = useState(null);
    const [loading, setLoading] = useState(true);
    const [code, setCode] = useState('');
    const [output, setOutput] = useState('');
    const [isRunning, setIsRunning] = useState(false);
    const [language, setLanguage] = useState('javascript');
    const [snackbar, setSnackbar] = useState({ 
        open: false, 
        message: '', 
        severity: 'success' 
    });
    const [executionTime, setExecutionTime] = useState(0);
    const [memoryUsage, setMemoryUsage] = useState(0);

    useEffect(() => {
        loadProject();
    }, [projectId]);

    const loadProject = async () => {
        try {
            setLoading(true);
            
            // Check if user is admin and use appropriate endpoint
            const isAdmin = user?.is_staff || user?.is_superuser;
            const response = isAdmin 
                ? await projectService.getProjectAdmin(projectId)
                : await projectService.getProject(projectId);
                
            setProject(response.data);
            setCode(response.data.content);
            
            // Set language based on project type
            if (response.data.language) {
                setLanguage(response.data.language);
            }
        } catch (error) {
            console.error('Error loading project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to load project',
                severity: 'error'
            });
        } finally {
            setLoading(false);
        }
    };

    const executeCode = async (code, language) => {
        try {
            // Check if user is admin and use appropriate endpoint
            const response = await projectService.executeCode(code, language);
                
            return response.data;
        } catch (error) {
            console.error('Error executing code:', error.response?.data || error.message);
            throw error;
        }
    };

    const getFileExtension = (lang) => {
        return LANGUAGE_MAP[lang]?.extension || 'txt';
    };

    const handleRun = async () => {
        if (!code.trim()) {
            setSnackbar({
                open: true,
                message: 'Please write some code first',
                severity: 'warning'
            });
            return;
        }

        setIsRunning(true);
        setOutput('');
        setExecutionTime(0);
        setMemoryUsage(0);

        try {
            const startTime = Date.now();
            
            const result = await executeCode(code, language);
            
            const endTime = Date.now();
            setExecutionTime(endTime - startTime);
            
            setOutput(result.output);
            setMemoryUsage(result.memory / 1024 || 0);
            
            setSnackbar({
                open: true,
                message: 'Code executed successfully!',
                severity: 'success'
            });
        } catch (error) {
            console.error('Execution error:', error);
            setOutput(`Error: ${error.message}`);
            setSnackbar({
                open: true,
                message: 'Code execution failed',
                severity: 'error'
            });
        } finally {
            setIsRunning(false);
        }
    };

    const handleSave = async () => {
        try {
            // Check if user is admin and use appropriate endpoint
            const isAdmin = user?.is_staff || user?.is_superuser;
            const updateMethod = isAdmin ? projectService.updateProjectAdmin : projectService.updateProject;
            
            await updateMethod(projectId, {
                ...project,
                content: code
            });
            
            setSnackbar({
                open: true,
                message: 'Code saved successfully!',
                severity: 'success'
            });
        } catch (error) {
            console.error('Error saving project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to save code',
                severity: 'error'
            });
        }
    };

    const handleDownload = () => {
        const element = document.createElement('a');
        const file = new Blob([code], { type: 'text/plain' });
        element.href = URL.createObjectURL(file);
        element.download = `${project?.title || 'project'}.${getFileExtension(language)}`;
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    };

    const handleSnackbarClose = () => {
        setSnackbar({ ...snackbar, open: false });
    };

    if (loading) {
        return (
            <RootWrapper>
                <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
                    <Typography sx={{ color: 'black' }}>Loading project...</Typography>
                </Box>
            </RootWrapper>
        );
    }

    if (!project) {
        return (
            <RootWrapper>
                <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
                    <Typography sx={{ color: 'black' }}>Project not found</Typography>
                </Box>
            </RootWrapper>
        );
    }

    const isOwner = project.owner.id === user?.id;
    const isCollaborator = project.collaborators?.some(
        (collab) => collab.user.id === user?.id && collab.role in ['editor', 'admin']
    );
    const isAdmin = user?.is_staff || user?.is_superuser;

    // if (!isOwner && !isCollaborator && !isAdmin) {
    //     return (
    //         <RootWrapper>
    //             <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
    //                 <Typography sx={{ color: 'black' }}>You don't have permission to edit this project.</Typography>
    //             </Box>
    //         </RootWrapper>
    //     );
    // }

    return (
        <>
            <Helmet>
                <title>Compiler | OneDollarAI</title>
            </Helmet>

            <RootWrapper>
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', px: 2, py: 1 }}>
                    <IconButton
                        edge="start"
                        onClick={() => navigate('/projects')}
                        sx={{ mr: 2, color: 'black' }}
                    >
                        <BackIcon />
                    </IconButton>
                    
                    <Breadcrumbs sx={{ flexGrow: 1 }}>
                        <Typography sx={{ color: 'black' }}>Compiler</Typography>
                    </Breadcrumbs>

                    <Box sx={{ display: 'flex', gap: 1 }}>
                        <FormControl size="small" sx={{ minWidth: 120 }}>
                            <InputLabel sx={{ color: 'black' }}>Language</InputLabel>
                            <Select
                                value={language}
                                onChange={(e) => setLanguage(e.target.value)}
                                label="Language"
                                sx={{ 
                                    color: 'black',
                                    '& .MuiOutlinedInput-notchedOutline': {
                                        borderColor: 'black'
                                    },
                                    '&:hover .MuiOutlinedInput-notchedOutline': {
                                        borderColor: 'black'
                                    },
                                    '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                                        borderColor: 'black'
                                    }
                                }}
                            >
                                {Object.values(LANGUAGE_MAP).map((lang) => (
                                    <MenuItem key={lang.name} value={lang.name} sx={{ 
                                        color: 'black !important',
                                        '&:hover, &:active, &.active, &.Mui-selected': {
                                            color: 'black !important'
                                        }
                                    }}>
                                        {lang.name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                        {/* <Tooltip 
                            title="Save Code"
                            componentsProps={{
                                tooltip: {
                                    sx: {
                                        bgcolor: 'black',
                                        color: 'white',
                                        '& .MuiTooltip-arrow': {
                                            color: 'black'
                                        }
                                    }
                                }
                            }}
                        >
                            <IconButton onClick={handleSave} sx={{ color: 'black' }}>
                                <SaveIcon />
                            </IconButton>
                        </Tooltip> */}

                        <Tooltip 
                            title="Download Code"
                            componentsProps={{
                                tooltip: {
                                    sx: {
                                        bgcolor: 'black',
                                        color: 'white',
                                        '& .MuiTooltip-arrow': {
                                            color: 'black'
                                        }
                                    }
                                }
                            }}
                        >
                            <IconButton onClick={handleDownload} sx={{ color: 'black' }}>
                                <DownloadIcon />
                            </IconButton>
                        </Tooltip>
                    </Box>
                </Box>

                <Box sx={{ flexGrow: 1, display: 'flex', overflow: 'hidden', maxHeight: 'calc(100% - 44px)' }}>
                    <Grid container sx={{ height: '100%' }}>
                        {/* Code Editor */}
                        <Grid 
                            item 
                            xs={12} 
                            md={6} 
                            sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                        >
                            <Paper 
                                sx={{ 
                                    flexGrow: 1, 
                                    display: 'flex', 
                                    flexDirection: 'column', 
                                    m: 1,
                                    maxHeight: 'calc(100% - 18px)'
                                }}
                            >
                                <Box sx={{ borderBottom: 1, borderColor: 'divider', p: 1 }}>
                                    <Typography 
                                        variant="h6" 
                                        sx={{ display: 'flex', alignItems: 'center', gap: 1, color: 'black' }}
                                    >
                                        <CodeIcon sx={{ color: 'black' }} />
                                        Code Editor
                                    </Typography>
                                </Box>
                                
                                <Box 
                                    display="flex" 
                                    flexDirection="column" 
                                    sx={{ flexGrow: 1, p: 2, maxHeight: 'calc(100% - 44px)' }}
                                >
                                    <CodeEditor
                                        value={code}
                                        onChange={setCode}
                                        language={language}
                                    />
                                </Box>
                            </Paper>
                        </Grid>

                        <Grid 
                            item 
                            xs={12} 
                            md={6} 
                            sx={{ height: '100%', display: 'flex', flexDirection: 'column', maxHeight: '100%' }}
                        >
                            <Paper 
                                sx={{ 
                                    flexGrow: 1, 
                                    display: 'flex', 
                                    flexDirection: 'column', 
                                    m: 1,
                                    maxHeight: 'calc(100% - 18px)',
                                    scrollbarColor: '#888 #ffffff'
                                }}
                            >
                                <Box sx={{ flexGrow: 1, p: 2, maxHeight: '100%' }}>
                                    <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column', maxHeight: '100%' }}>
                                        <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
                                            <Button
                                                variant="contained"
                                                startIcon={<RunIcon />}
                                                onClick={handleRun}
                                                disabled={isRunning}
                                                color="success"
                                            >
                                                {isRunning ? 'Running...' : 'Run Code'}
                                            </Button>
                                        </Box>

                                        <Box sx={{ display: 'flex', maxHeight: '100%', gap: 2, mb: 2 }}>
                                            <Chip 
                                                label={`Time: ${executionTime}ms`} 
                                                color="primary" 
                                                variant="outlined"
                                            />
                                            <Chip 
                                                label={`Memory: ${memoryUsage.toFixed(2)}KB`} 
                                                color="secondary" 
                                                variant="outlined"
                                                sx={{ color: 'black' }}
                                            />
                                        </Box>

                                        <Box
                                            sx={{
                                                bgcolor: "white",
                                                color: '#667085',
                                                boxShadow: "0px 0px 2px #6A7199",
                                                padding: "10px",
                                                borderRadius: "10px",
                                                wordWrap: 'break-word',
                                                whiteSpace: 'pre-wrap',
                                                overflowWrap: 'break-word',
                                                maxHeight: '100%',
                                                overflowY: 'auto'
                                            }}>
                                                <Typography lineHeight={2}>{output || 'Output will appear here...'}</Typography>
                                        </Box>
                                    </Box>
                                </Box>
                            </Paper>
                        </Grid>
                    </Grid>
                </Box>
            </RootWrapper>

            <Snackbar
                open={snackbar.open}
                autoHideDuration={6000}
                onClose={handleSnackbarClose}
            >
                <Alert
                    onClose={handleSnackbarClose}
                    severity={snackbar.severity}
                    sx={{ width: '100%' }}
                >
                    {snackbar.message}
                </Alert>
            </Snackbar>
        </>
    );
};

export default ProjectRun; 